
import { MDBIcon, MDBListGroupItem } from 'mdb-vue-ui-kit'
import { defineComponent } from 'vue'

import router from '@/router'

export default defineComponent({
  name: 'HomeMenuItem',
  components: {
    MDBIcon,
    MDBListGroupItem,
  },
  props: {
    iconName: {
      type: String,
      required: true,
    },
    itemText: {
      type: String,
      required: true,
    },
    animationName: {
      type: String,
      required: true,
    },
    sortParameter: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const pushRouter = (): void => {
      router.push({
        name: 'home-products',
        params: { sortParameter: props.sortParameter },
      })
    }
    return { pushRouter }
  },
})
